.hero {
  background-size: 100vw, 100vh, cover;
  color: white;
  width: 100vw;
  height: 100vh;
  position: relative;
  display: grid;
  -ms-flex-align: center;
      align-items: center;
}

.hero::after {
  background-color: black;
  opacity: 0.4;
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
}


